x h5 {\n  letter-spacing: 2px;\n  font-style: medium;\n  font: 15px \"Lato\";\n  color: #311c63;\n  text-align: center;\n}\n\n#inbox  .container {\n  background-color: #FAFAFA;\n}\n\n#inbox .row#header {\n  margin-top: 25px;\n}\n\n#inbox p {\n  font-size: 15px;\n}\n#inbox p.lead {\n  font-size: 17px;\n}\n\n#inbox .aside {\n  display:block;\n  background-color: #F9F9F9;\n  border-right: 1px solid #DEDEDE;\n}\n\n#inbox .invoice-editor {\nbackground-color: white;\n}\n\n#inbox .messages {\n  display:block;\n  background-color: #fff;\n  border-right: 1px solid #DEDEDE;\n}\n#inbox .message {\n  display:block;\n  background-color: #fff;\n}\n\n#inbox .aside .compose {\n  height: 95px;\n  margin:0 -10px;\n  padding: 25px 30px;\n}\n\n#inbox .aside .compose .button {\n  color: #F6F7F7;\n}\n#inbox .aside .compose .button .compose {\n  font-size: 18px;\n  font-weight: 700;\n}\n#inbox .aside .main-inbox {\n  padding: 40px;\n  color: #6F7B7E;\n}\n#inbox .aside .title {\n  color: #6F7B7E;\n  font-size: 16px;\n  font-weight: bold;\n  text-transform: uppercase;\n}\n#inbox .aside .main-inbox .item {\n  display: block;\n  padding: 10px 0;\n  color: #6F7B7E;\n}\n#inbox .aside .main-inbox .item.active {\n  background-color: #F1F1F1;\n  margin: 0 -50px;\n  padding-left: 50px;\n}\n#inbox .aside .main-inbox .item:active,.aside .main-inbox .item:hover {\n  background-color: #F2F2F2;\n  margin: 0 -50px;\n  padding-left: 50px;\n}\n#inbox .aside .main-inbox .icon {\n  font-size: 22px;\n  padding-right: 30px;\n  color: #A0A0A0;\n}\n#inbox .aside .main-inbox .name {\n  font-size: 18px;\n  color: #5D5D5D;\n  font-weight: 500;\n}\n#inbox .messages {\n  padding: 40px 20px;\n}\n#inbox .message {\n  padding: 40px 20px;\n}\n#inbox .messages .action-buttons {\n  padding: 0;\n  margin-top: -20px;\n}\n#inbox .message .action-buttons {\n  padding: 0;\n  margin-top: -5px;\n}\n\n#inbox .pg {\n  display: inline-block;\n  top:10px;\n}\n\n#inbox .inbox-messages {\n  border: #232B2D;\n  border-width: 5px;\n  margin-top:60px;\n}\n\n#inbox .inbox-messages .card-content {\n  border-style: solid;\n  border-width: 0.5px;\n  width: 100%;\n}\n#inbox .inbox-messages strong {\n  color: #5D5D5D;\n}\n#inbox .inbox-messages .msg-check {\n  padding: 0 20px;\n}\n#inbox .inbox-messages .msg-subject {\n  padding: 10px 0;\n  color: #5D5D5D;\n}\n#inbox .inbox-messages .msg-attachment {\n  float:right;\n}\n#inbox .inbox-messages .msg-snippet {\n  padding: 5px 20px 0px 5px;\n}\n#inbox .inbox-messages .msg-subject .fa {\n  font-size: 18px;\n  padding:3px 0;\n}\n#inbox .inbox-messages .msg-timestamp {\n  float: right;\n  padding: 0 20px;\n  color: #5D5D5D;\n}\n#inbox .message-preview .avatar {\n  display: inline-block;\n}\n#inbox .message-preview .top .address {\n  display: inline-block;\n  padding: 0 20px;\n}\n\n\n\n#inbox .avatar img {\n  width: 40px;\n  border-radius: 50px;\n  border: 2px solid #999;\n  padding: 2px;\n}\n#inbox .address .name {\n  font-size: 20px;\n  font-weight: bold;\n}\n#inbox .address .email {\n  font-weight: bold;\n  color: #B6C7D1;\n}\n#inbox .card.active {\n  background-color:#F5F5F5;\n}\n\n/* lock toggle */\n\n#inbox .switch {\n  position: relative;\n  display: inline-block;\n  width: 50px;\n  height: 24px;\n}\n\n#inbox .switch input { \n  opacity: 0;\n  width: 0;\n  height: 0;\n}\n\n#inbox .slider {\n  position: absolute;\n  cursor: pointer;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  background-color: #ccc;\n  -webkit-transition: .4s;\n  transition: .4s;\n  border-radius: 28px;\n}\n\n#inbox .slider:before {\n  position: absolute;\n  content: \"\";\n  height: 20px;\n  width: 20px;\n  left: 2px;\n  bottom: 2px;\n  background-color: white;\n  -webkit-transition: .4s;\n  transition: .4s;\n  border-radius: 50%;\n}\n\n#inbox input:checked + .slider {\n  background-color: #87C27B;\n}\n\n#inbox input:focus + .slider {\n  box-shadow: 0 0 1px #87C27B;\n}\n\n#inbox input:checked + .slider:before {\n  -webkit-transform: translateX(26px);\n  -ms-transform: translateX(26px);\n  transform: translateX(26px);\n}\n\n#inbox .reply_msg{\n  padding-left: 10px;\n  padding-bottom: 50px;\n}\n\n/* recup */\n\n#inbox  .message-wrapper .panel {\n  border-top: none;\n  margin-top: 10px;\n}\n\n#inbox .list-message .list-group-item {\n  padding: 15px;\n  color: #999999 !important;\n  /* border-right: 3px solid #8CC152 !important; */\n}\n\n#inbox .list-message .pending-order {\n  border-right: 3px solid #a255ff !important;\n}\n\n#inbox .list-message .request {\n  border-right: 3px solid #8CC152 !important;\n}\n\n#inbox .list-message #responded {\n  background-color: #f2f1f5;\n}\n\n#inbox .list-message #unresponded {\n  background-color: white;\n}\n\n#inbox .avatar{\n  width:50px;\n  height:50px;\n}\n\n/* scrolly bars */\n#inbox .col-md-4.message-sideleft .panel-body {\n  overflow-y: auto;\n  height: 100%;\n}\n#inbox .message-wrapper .col-md-8.message-sideright {\n  margin-top:22px;\n  overflow-y: auto;\n  height: 500px;\n}\n#inbox .message-wrapper .invoice-generator{\n  min-width: 320px;\n}\n\n.invoice-preview{\n  padding:20px;\n  margin:10px;\n  border: 2px dotted #0a00b6;\n}\n\n/* EXTRA */\n\n/* body{margin-top:20px;\n  background:#eee;\n  } */\n  \n  /* ========================================================================\n   * MESSAGES\n   * ======================================================================== */\n  /* .message form {\n    padding: 6px 15px;\n    background-color: #FAFAFA;\n    border-bottom: ;\n  }\n  .message form .has-icon .form-control-icon {\n    position: absolute;\n    z-index: 5;\n    top: 0;\n    right: 0;\n    width: 34px;\n    line-height: 33px;\n    text-align: center;\n    color: #777;\n  }\n  .message > a {\n    position: relative;\n  }\n  .message .indicator {\n    text-align: center;\n  }\n  .message .indicator .spinner {\n    left: 26%;\n    width: 200px;\n    font-size: 13px;\n    line-height: 17px;\n    color: #999;\n  }\n  \n  .message-wrapper {\n    position: relative;\n    padding: 0px;\n    background-color: #ffffff;\n    margin: 0px;\n  }\n  .message-wrapper .message-sideleft {\n    vertical-align: top !important;\n  }\n  .message-wrapper .message-sideleft[class*=\"col-\"] {\n    padding-right: 0px;\n    padding-left: 0px;\n  }\n\n  .message-wrapper .message-sideright[class*=\"col-\"] {\n    padding: 30px;\n  }\n  .message-wrapper .message-sideright .panel {\n    border-top: 1px dotted #DDD;\n    padding-top: 20px;\n  }\n  .message-wrapper .message-sideright .panel:first-child {\n    border-top: none;\n    padding-top: 0px;\n  }\n  .message-wrapper .message-sideright .panel .panel-heading {\n    border-bottom: none;\n  }\n  .message-wrapper .panel {\n    background-color: transparent !important;\n    -moz-box-shadow: none !important;\n    -webkit-box-shadow: none !important;\n    box-shadow: none !important;\n  }\n  .message-wrapper .panel .panel-heading, .message-wrapper .panel .panel-body {\n    background-color: transparent !important;\n  }\n  .message-wrapper .media .media-body {\n    font-weight: 300;\n  }\n  .message-wrapper .media .media-heading {\n    margin-bottom: 0px;\n  }\n  .message-wrapper .media small {\n    color: #999999;\n    font-weight: 400;\n  }\n  \n   */\n\n   #inbox .list-message .list-group-item.active {\n    background-color: #EEEEEE;\n    border-bottom: 1px solid #DDD !important;\n  }\n  #inbox .list-message .list-group-item.active p {\n    color: #999999 !important;\n  }\n  #inbox .list-message .list-group-item.active:hover, .list-message .list-group-item.active:focus, .list-message .list-group-item.active:active {\n    background-color: #EEEEEE;\n  }\n  #inbox .list-message .list-group-item small {\n    font-size: 12px;\n  \n  }\n  #inbox .list-message .list-group-item .list-group-item-heading {\n    color: #999999 !important;\n  }\n  #inbox .list-message .list-group-item .list-group-item-text {\n    margin-bottom: 10px;\n  }\n  #inbox .list-message .list-group-item:last-child {\n    -moz-border-radius: 0px;\n    -webkit-border-radius: 0px;\n    border-radius: 0px;\n    border-bottom: 1px solid #DDD !important;\n  }\n\n  "],"sourceRoot":""}]);

