  border-radius: 5px;\n}\n\n\n.dashboard #subservice-block {\n  min-width: 95%; \n  margin-left: 40px;\n}\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n.dashboard .service-block-sub #service-title{\n  background-color: #301a70;\n  padding-left: 10px;\n  margin-right: 10px;\n  padding-top:0px;\n  border-radius: 5px;\n  box-shadow:0px 0px 0px 0px;\n}\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n/* Profile photo - company logo */\n.circle,\n#logo {\n  background-color: white;\n  width: 150px;\n  height: 150px;\n  -moz-border-radius: 75px;\n  -webkit-border-radius: 75px;\n  border-radius: 75px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  /* background-image: url(\"./Icons/noun_DNA_2362091.png\"); */\n  background-image: url(\"./noun_Science_profile_crop_purple.png\");\n}\n\n#dashboard #logo-border {\n  background-color: #301a70;\n  width: 160px;\n  height: 160px;\n  -moz-border-radius: 85px;\n  -webkit-border-radius: 85px;\n  border-radius: 85px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 5px;\n}\n\n#price-chart {\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  max-width: 800px;\n}\n\n/*------------- Text ------------------------*/\n\n#dashboard .md-title {\n  font-size: 25px;\n  color: #301a70;\n}\n\n#dashboard .md-title {\n  font-size: 25px;\n  color: #301a70;\n}\n\n#dashboard #service_logo {\n  color: #301a70;\n  font-size: 25px;\n}\n\n.dashboard #dashboard h1 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 35px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #dashboard h2 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: left;\n}\n\n.dashboard #dashboard h3 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #services h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block-sub h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n#dashboard #about h4 {\n  /* font-style: medium; */\n  font: 19px \"Roboto\";\n  color: black;\n  text-align: left;\n}\n\n#dashboard #about p {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 17px \"Roboto\";\n  color: black;\n  text-align: center;\n\n}\n\n.dashboard #dashboard #stats h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 18px \"Roboto\";\n  text-align: center;\n}\n\n\n\n#dashboard #service-desc h5 {\n  font: 15px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #services h6 {\n  font: 17px \"Roboto\";\n  color: white;\n}\n\n#dashboard #stats h6 {\n  font: 15px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n.card-text {\n  font: 18px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard p {\n  font: 20px \"Roboto\";\n  color: #111;\n  text-align: center;\n}\n\n#dashboard #price-chart p  {\n  font: 16px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #spec_inquiry p {\n  text-align: left;\n}\n\n.md-checkbox {\n  display: flex;\n}\n\n#add_service{\n  padding-top:10px;\n}\n\n#service-form-block {\n  color:#301a70;\n  border: 5px #301a70 solid;\n  padding: 30px;\n}\n\n#service-form-block table {\n  width: 100%;\n  table-layout: fixed;\n  background: #ebe8f1;\n}\n\n#subservice-form-block {\n  color:#301a70;\n}\n\n\n/* Responsive Text */\n\na {\n  color: #311c63;\n  -webkit-transition: all 0.35s;\n  transition: all 0.35s;\n}\n\na:hover,\na:focus {\n  color: #6a2c86;\n}\n\nhr {\n  max-width: 100px;\n  margin: 25px auto 0;\n  border-width: 1px;\n  border-color: rgba(34, 34, 34, 0.1);\n}\n\np {\n  font-size: 18px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n  margin-bottom: 20px;\n}\n\n.form p {\n  font-size: 12px;\n  line-height: 1.5;\n  /* color: #dad8d8; */\n}\n\n.expand-enter-active,\n.expand-leave-active {\n  transition-property: opacity, height;\n}\n\n.expand-enter,\n.expand-leave-to {\n  opacity: 0;\n}\n\n/* ------------- buttons ----------------- */\n/* The following button css was copied directly from FigAnal for reference */\n\n/* Stats View Leads */\n\n.btn-quote-req{\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n}\n\n.btn-gen-inq {\n  border-radius: 0px;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 12px;\n  text-transform: uppercase;\n  font-style: medium;\n  margin: 0 auto;\n  justify-content: center;\n  background-color: #2238ff;\n}\n\n#btn-orders {\n  background-color: #8164d8;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 15px;\n  color: white;\n  border-radius: 5px;\n}\n\n#btn-orders:hover, #btn-orders:focus, #btn-orders:active {\n  background-color: #6545c5;\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 15px;\n  color: white;\n  border-radius: 5px;\n}\n\n#btn-leads {\n  background-color: rgb(128, 207, 0);\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 15px;\n  color: white;\n  border-radius: 5px;\n}\n\n#btn-leads:hover, #btn-leads:focus, #btn-leads:active {\n  background-color: rgb(156, 226, 43);\n  font-family: 'Roboto', sans-serif;\n  letter-spacing: 2px;\n  font-size: 15px;\n  color: white;\n  border-radius: 5px;\n}\n\n/* ------Vue Directive Responsive Design Ish ----------------------------------------------------*/\n\n.responsive-component.small body.dashboard {\n  text-align: center;\n  text-align: justify;\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  min-height: 100vh;\n  padding: 10px 0px;\n  background: linear-gradient(to bottom right, #ffffff 0%, #d8d4dc 100%);\n} \n\n.responsive-component.small #add-services {\n  margin: 10px;\n}\n\n/* logo */\n.responsive-component.small #dashboard #logo {\n  background-color: white;\n  width: 110px;\n  height: 110px;\n  -moz-border-radius: 55px;\n  -webkit-border-radius: 55px;\n  border-radius: 55px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  /* background-image: url(\"./Icons/noun_DNA_2362091.png\"); */\n  background-image: url(\"./noun_Science_profile_crop_purple.png\");\n}\n.responsive-component.small #dashboard #logo-border {\n  background-color: #301a70;\n  width: 120px;\n  height: 120px;\n  -moz-border-radius: 60px;\n  -webkit-border-radius: 60px;\n  border-radius: 60px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 5px;\n}\n.responsive-component.small .dashboard #subservice-block {\n  min-width: 95%; \n  margin-left: 20px;\n}\n\n#service-form-block{\n  padding:10px;\n}\n\n\n\n","<template>\n<div id=\"app\">\n  <dashboard-header />\n  <body class=\"dashboard\">\n    <!--  -->\n\n    <div\n      class=\"payment-cards\"\n      v-for=\"source in this.customerSources\"\n      v-bind:key=\"source.id\"\n      v-on:click=\"promptForDefaultSourceConfirm(source.id)\"\n    >\n      <pre>{{ source.brand }} ending in\t{{ source.last4 }} </pre>\n      <pre> expires : {{ source.exp_month }} / {{ source.exp_year }}</pre>\n    </div>\n\n    <!--  -->\n  </body>\n\n  <!-- Prompt to ask the user if they want to make this card the default source -->\n  <modal name=\"change-default-source-prompt\">\n    <div class=\"container\" style=\"margin:75px\">\n      <div class=\"row\">\n        <div class=\"col-md-4\">\n          <h4>Would you like to make this card your default payment method ?</h4>\n\n          <button\n            class=\"btn btn-info pull-right\"\n            style=\"margin-top:10px\"\n            type=\"button\"\n            @click=\"makeDefaultSource\"\n          >Submit</button>\n        </div>\n      </div>\n    </div>\n  </modal>\n  <!--  -->\n\n  <!-- Tells the user that the default source has been changed -->\n  <modal name=\"default-source-changed\">\n    <div class=\"container\" style=\"margin:75px\">\n      <div class=\"row\">\n        <div class=\"col-md-4\">\n          <h4>Your default payment method has been changed</h4>\n\n          <button\n            class=\"btn btn-info pull-right\"\n            style=\"margin-top:10px\"\n            type=\"button\"\n            @click=\"hideConfirmMessage\"\n          >OK Great</button>\n        </div>\n      </div>\n    </div>\n  </modal>\n  <!--  -->\n</div>\n</template>\n\n<script>\nimport DashboardHeader from \"@/components/sellerComponents/DashboardHeader.vue\";\nimport SettingsService from \"@/services/SettingsService\";\n\nexport default {\n  data() {\n    return {\n      user: null,\n      customerSources: [],\n      sourceToBeDefaulted: \"\"\n    };\n  },\n  mounted() {\n    this.getStripeUserInfo();\n  },\n  components: {\n    DashboardHeader\n  },\n  methods: {\n    // Takes the source ID provided and sets it as the default source\n    async hideConfirmMessage() {\n      this.$modal.hide(\"default-source-changed\");\n    },\n    async makeDefaultSource() {\n      try {\n        const userExtracted = this.$store.getters.getUserInfo;\n\n        const updateDefaultSourceInfo = {\n          stripeCustomerId: userExtracted.stripeCustomerId,\n          sourceID: this.sourceToBeDefaulted\n        };\n        this.$modal.hide(\"change-default-source-prompt\");\n        const response = await SettingsService.updateDefaultSource(\n          updateDefaultSourceInfo\n        );\n        console.log(`\\nResponse for makeDefaultSource : ${response}\\n`);\n        this.$modal.show(\"default-source-changed\");\n      } catch (error) {\n        console.log(`\\nThe error inside of makeDefaultSource: ${error}`); // TESTING\n        if (error) throw error;\n      }\n    },\n    async promptForDefaultSourceConfirm(sourceID) {\n      try {\n        this.$modal.show(\"change-default-source-prompt\");\n        this.sourceToBeDefaulted = sourceID;\n      } catch (error) {\n        console.log(\n          `\\nAn error occurred in promptForDefaultSourceConfirm ${error}\\n`\n        );\n        if (error) throw error;\n      }\n    },\n    async getStripeUserInfo() {\n      try {\n        const userExtracted = this.$store.getters.getUserInfo;\n        const stripeUserInfo = await SettingsService.getStripeUserInfo(\n          userExtracted\n        );\n        this.subscriptions = stripeUserInfo.data.subscriptions.data;\n        const customerSources = stripeUserInfo.data.stripeCustomer.sources;\n        this.customerSources = customerSources.data;\n\n        console.log(\n          `\\nThe customer's default source is : ${JSON.stringify(\n            stripeUserInfo.data.stripeCustomer.default_source\n          )}\\n`\n        ); // TESTING\n      } catch (error) {\n        console.log(\n          `\\nAn error occurred in SettingsChangeDefaultSource getStripeUserInfo ${error}\\n`\n        );\n        if (error) throw error;\n      }\n    }\n  }\n};\n</script>\n\n<style>\n@import \"../../assets/css/dashboard.css\";\n.payment-cards {\n  width: 400px;\n  font-size: 150%;\n  border: 2px solid purple;\n  padding: 20px;\n  margin-left: 10px;\n}\n</style>\n"],"sourceRoot":""}]);

