n:center;\n    padding-top: 30px;\n    min-height: 100vh; \n}\n\n#login a, p {\n    text-align:center;\n    font: 14px 'Roboto';\n    color: #29105b;\n}\n\n#login h1 {\n    font: 28px 'Roboto';\n    color: #29105b;\n    text-align:center;\n}\n\n#login h2 {\n    color: #29105b;\n    text-align:center;\n}\n\n#form-footer{\n    padding-bottom:5px;\n    padding-top:15px;\n    text-align: center;\n}\n#form-footer p {\n    text-align: center;\n    font: 12px 'Roboto';\n    color: rgb(129, 114, 114);\n}\n\n\n/* Card Formatting */\n\n.md-card {\n    margin-bottom: 0px;\n    max-width:700px;\n    margin-left: auto;\n    margin-right:auto;\n    border-radius: 10px;\n    background-color:white;\n}\n\n/* .card #rounded {\n    border-radius: 10px;\n} */\n\n.md-steppers {\n    color: #361555;\n}\n\n/* Container Formatting */\n\n.container#center {\n    /* margin-left: 0 auto;  */\n    /* shorthand for:\n    margin-top: 0;\n    margin-bottom: 0;\n    margin-left: auto;\n    margin-right: auto; */\n    text-align:center;\n}\n\n.container p {\n    text-align: center;\n    font: 12px 'Roboto';\n    color: rgb(129, 114, 114);\n}\n\n/* --------- Register Form---------- */\n\n\n\nform {\n    border-radius: 5px;\n    max-width:600px;\n    width:100%;\n    /* margin: 5% auto; */\n    padding: 5px 15px;\n    background-color: #FFFFFF;\n    overflow: hidden; \n    border-radius: 25px;\n}\n\n.center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 100%;\n    height:auto;\n    animation-name: moveInLeft;\n    animation-duration: 2s;\n}\n\n#register{\n    border-radius: 10px;\n}\n\n#register, label{\n    font: 18px 'Roboto';\n    color: rgb(94, 94, 94);\n    text-align:left;\n}\n\n#personalinfo {\n    width: 49.5%; \n    float:left;\n    border-right: 1px dotted #CCC;\n    box-sizing: border-box;\n    padding: 0px 15px 0px 0px;\n    overflow: hidden;\n    clear: both;\n}\n  \n#companyinfo {\n    width:49.5%;\n    float:right;\n    box-sizing: border-box;\n    padding: 0px 0px 0px 15px;\n    overflow: hidden;\n    clear: both;\n}\n\n#register-form-footer{\n    border-top: 2px #917e9e;\n}\n\n\n.fixed-horizontal-center {\n    display: block;\n    margin-left: auto;\n    margin-right: auto;\n    width: 800px;\n}\n\n.error {\n    color: red;\n}\n\n#sign-up-link{\n    color: #850696;\n}\n#sign-up-link:hover, \n#sign-up-link:active, \n#sign-up-link:focus {\n    color: #b109c7;\n}\n\np {\n    text-align: left;\n    color: white;\n    /* font-size: 15px */\n    font: 20px \"roboto\";\n}\n\n#btn-login {\n    color: white;\n    border-color: #850696;\n    background-color: #850696;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 20px;\n}\n\n#btn-login:hover, \n#btn-login:active, \n#btn-login:focus {\n    color: white;\n    border-color: #9c07af;\n    background-color: #9c07af;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 20px;\n}\n\n#btn-signup {\n    display: inline-block;\n    border-radius: 5px;\n    height:40px;\n    width:200px;\n    border-color:#29105b;\n}\n\n#btn-submit:hover {\n    color: white;\n    border-color: #850696;\n    background-color: #850696;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-next {\n    color: white;\n    border-color: #850696;\n    background-color: #850696;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n#btn-next:hover {\n    color: white;\n    border-color: #b109c7;\n    background-color: #b109c7;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-previous {\n    color: #850696;\n    border-color: #850696;\n    background-color: white;\n    justify-content: center;\n    display: inline-block;\n    border-radius: 5px;\n}\n\n#btn-previous:hover {\n    color: #a109c7;\n    border-color: #a109c7;\n    background-color: rgb(228, 228, 228);\n    justify-content: center;\n    display: inline-block;\n    border-