er-style: solid;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n  border-radius: 5px;\n  text-align: left;\n  padding: 15px;\n}\n\n#dashboard #about .md-content {\n  max-width: 1200px;\n  max-height: 250px;\n  overflow: auto;\n}\n\n#dashboard #stats {\n  border-radius: 5px;\n}\n#services .md-empty-state{\n  color: #9a92af;\n}\n\n#stats .card {\n  border-radius: 5px;\n  margin-left:5px;\n  margin-right:5px;\n  box-shadow:   2px 2px 5px 1px  #c7c4d1;\n}\n\n#dashboard .service-border {\n  border-bottom: 2px solid #301a70;\n}\n\n#dashboard .md-checkbox {\n  display: flex;\n}\n\n#dashboard #service-card .md-card-content {\n  max-width: 900px;\n  max-height: 200px;\n  overflow: auto;\n}\n\n#dashboard .md-card {\n  width: 320px;\n  margin: 4px;\n  display: inline-block;\n  vertical-align: top;\n}\n\n\n\n.dashboard #services {\n  padding-left: 20px;\n  padding-right: 20px;\n  padding-top: 0px;\n  background-color: white ;\n  color:black;\n  box-shadow: 1px 1px 2px 1px  #9f9aac;\n  border-radius: 5px;\n}\n\n\n.dashboard #subservice-block {\n  min-width: 95%; \n  margin-left: 40px;\n}\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n.dashboard .service-block-sub #service-title{\n  background-color: #301a70;\n  padding-left: 10px;\n  margin-right: 10px;\n  padding-top:0px;\n  border-radius: 5px;\n  box-shadow:0px 0px 0px 0px;\n}\n\n.dashboard .service-block #service-desc{\n  background-color: white;\n  border-radius: 5px;\n}\n\n/* Profile photo - company logo */\n.circle,\n#logo {\n  background-color: white;\n  width: 150px;\n  height: 150px;\n  -moz-border-radius: 75px;\n  -webkit-border-radius: 75px;\n  border-radius: 75px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  /* background-image: url(\"./Icons/noun_DNA_2362091.png\"); */\n  background-image: url(\"./noun_Science_profile_crop_purple.png\");\n}\n\n#dashboard #logo-border {\n  background-color: #301a70;\n  width: 160px;\n  height: 160px;\n  -moz-border-radius: 85px;\n  -webkit-border-radius: 85px;\n  border-radius: 85px;\n  background-repeat: no-repeat;\n  background-size: cover;\n  padding: 5px;\n}\n\n#price-chart {\n  border-color: rgb(190, 187, 187);\n  border-style: solid;\n  border-width: .5px;\n  max-width: 800px;\n}\n\n/*------------- Text ------------------------*/\n\n#dashboard .md-title {\n  font-size: 25px;\n  color: #301a70;\n}\n\n#dashboard .md-title {\n  font-size: 25px;\n  color: #301a70;\n}\n\n#dashboard #service_logo {\n  color: #301a70;\n  font-size: 25px;\n}\n\n.dashboard #dashboard h1 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 35px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #dashboard h2 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: left;\n}\n\n.dashboard #dashboard h3 {\n  letter-spacing: 1px;\n  /* text-transform: uppercase; */\n  font-style: medium;\n  font: 25px \"Roboto\";\n  color: #311c63;\n  text-align: center;\n}\n\n.dashboard #services h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n.dashboard .service-block-sub h4 {\n  letter-spacing: 1px;\n  font: 20px \"Roboto\";\n  color: #311c63;\n}\n\n#dashboard #about h4 {\n  /* font-style: medium; */\n  font: 19px \"Roboto\";\n  color: black;\n  text-align: left;\n}\n\n#dashboard #about p {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 17px \"Roboto\";\n  color: black;\n  text-align: center;\n\n}\n\n.dashboard #dashboard #stats h4 {\n  letter-spacing: 1px;\n  /* font-style: medium; */\n  font: 18px \"Roboto\";\n  text-align: center;\n}\n\n\n\n#dashboard #service-desc h5 {\n  font: 15px \"Roboto\";\n  color: #111;\n}\n\n#dashboard #services h6 {\n  font: 17px \"Roboto\";\n  color: white;\n}\n\n#dashboard #stats h6 {\n 